	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>🌡26℃ 🌦</strong></p>\n<p>今日は半ドンの日なので、学校から帰って昼食を取ってから、母の畑へと向かいました。\nここで畑を借り、家庭菜園をしているのです。</p>\n<p>以前はナスとコスモスを植えていましたが、今度はトウモロコシと胡瓜、そして枝豆に挑戦するそうです。\n僕も手伝って、ミニトマトとサニーレタスのタネを蒔く作業をしました。</p>","frontmatter":{"cover":null,"date":"1990-06-17T21:55:00+09:00","strDate":"1990/6/17","title":"日記"}}},"pathContext":{"path":"/ja/blog/1990/06/18-diary/","langKey":"ja"}}

